<template>
  <div class="page">
    <product-header :product_id="productData.product_id"></product-header>
    <page-content>
      <div v-if="dataLoaded === false" class="flex-center pageIsLoading">
        <i class="fa fa-spinner fa-pulse fa-3x fa-fw" style="color: black"></i>
      </div>
      <div v-else-if="dataLoaded === true">
        <!-- <scroll :on-infinite="onInfinite" :enableRefresh=false :enableInfinite="!flagLoaded" :infiniteLoading="reloadStatus"> -->
          <div class="slider">
            <div class="prodName max-width">
              <span>{{ productData.label }}</span>
              <div v-if="productData.best" class="hitClass">
                <span></span>
              </div>
            </div>
          </div>
          <slide-wrapper class="sliderWrapper">
            <slide v-for="img in productData.gallery"  :key="img.id"><img class="sliderImg" v-lazy="img.mini" alt=" "/></slide>
          </slide-wrapper>
          <div class="prodPrice max-width">
            <span> {{productData.cost}} .</span>
          </div>
          <shop-button @click.native="onReserveClicked" caption=" " class="reserveBtn max-width" ></shop-button>
          <div class="prodQty max-width">
            <span>    10 </span>
          </div>
          <hr class="max-width hr" color="gray" size="1px"/>
          <div class="prodDescription max-width">
            {{ productData.description }}
          </div>
          <hr class="max-width hr" color="gray" size="1px"/>
          <div class="btnArea max-width">
            <button-small @click.native="onMarkedClicked" caption=" " class="markedBtn" ></button-small>
            <button-small @click.native="onSeenClicked" caption="" class="seenBtn" ></button-small>
          </div>
          <!--
          <div style="position: relative; width: 100%;" :style="{'height': this.$store.getters.getWindowSize.height + 'px'}">
            <div class="content-layout">
              <div class="content-padded">
                <custom-data-grid url="/products" :onReload="onReload" :columnNum="2" :elementHeight="getElementHeight" @flagLoaded="onFlagLoaded">
                  <template slot="content" scope="props">
                    <product-card-banner v-for="item in props.dataList" :key="item.id" :bannerData="item" @click.native="onProductClicked(item)" class="listItem"></product-card-banner>
                  </template>
                </custom-data-grid>
              </div>
            </div>
          </div>
          -->
        <!-- </scroll> -->
      </div>
    </page-content>
  </div>
</template>

<script>
  import { SlideWrapper, Slide } from '~/components/slide'
  import ProductHeader from 'appComponents/components/headers/ProductHeader.vue'
  import Button from 'appComponents/components/buttons/Button.vue'
  import ButtonSmall from 'appComponents/components/buttons/ButtonSmall.vue'
  import ProductCardBanner from 'appComponents/components/banners/ProductCardBanner.vue'
  import CustomDataGrid from 'appComponents/components/banners/CustomDataGrid.vue'
  import Scroll from '~/components/customScroll'
  import Content from '~/components/content'

  export default {
    components: {
      ProductHeader,
      'shop-button': Button,
      ButtonSmall,
      SlideWrapper,
      Slide,
      ProductCardBanner,
      CustomDataGrid,
      Scroll,
      'page-content': Content
    },
    data () {
      return {
      }
    },
    created: function () {
      let id = this.$route.params.id
      this.$store.dispatch('getProduct', {id: id})
    },
    computed: {
      productData () {
        return this.$store.getters.getProductsItem
      },
      dataLoaded () {
        return this.$store.getters.getProductItemLoaded
      }
    },
    methods: {
      onReserveClicked () {
        this.$store.commit('setCardCode', this.$route.params.id)
        this.$router.push({
          name: 'reserve'
        })
      },
      onMarkedClicked () {
        console.log('clicked')
      },
      onSeenClicked () {
        console.log('clicked')
      },
      onProductClicked (item) {
        this.$router.push({
          name: 'reserve',
          params: {
            product: item.id
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .slider
  {
    padding-top: 20px;
  }

  .hr {
    width: 80%;
    text-align: center;
  }

  .sliderWrapper
  {
    width: 100%;
  }

  .prodName
  {
    height: 24px;
    text-align: center;
    width: 80%;
    margin: 0 auto;
  }

  .prodName > span
  {
    font-size: 16px;
  }

  .prodPrice
  {
    height: 24px;
    text-align: center;
    width: 80%;
    margin: 5px auto;
    padding-bottom: 35px;
  }

  .prodQty
  {
    height: 24px;
    text-align: center;
    width: 80%;
    margin: 5px auto;
    padding-bottom: 35px;
  }

  .prodQty > span
  {
    font-size: 12px;
    line-height: 22px;
  }

  .hitClass
  {
    width: 48px;
    height: 32px;
    position: absolute;
    text-align: center;
    top: 0;
    right: 0;
    background-color: antiquewhite;
  }

  .hitClass > span
  {
    line-height: 32px;
    font-size: 12px;
  }

  .sliderImg
  {
    width: 200px;
    height: auto;
  }

  .prodDescription
  {
    margin: 5px auto;
    width: 80%;
    text-align: center;
  }

  .btnArea
  {
    width: 80%;
    height: 39px;
    margin-top: 5px;
    margin-bottom: 5px;
  }

  .markedBtn
  {
    height: 100%;
    width: 50%;
    text-align: center;
    display: inline-block;
    float: left;
  }

  .seenBtn
  {
    height: 100%;
    width: 50%;
    text-align: center;
    display: inline-block;
    float: left;
  }
</style>



