<template>
  <div>
    <TitleHeader title=" "></TitleHeader>
    <page-content class="content-padding-bottom">
      <div class="content-layout">
        <person-card :personData="personData"></person-card>
        <list>
          <list-item v-for="item in menuData" :key="item.id" @click.native="onItemClicked(item)">
            <div class="item-content">
              <div class="item-title-row" style="height: 40px;">
                <div class="item-title" style="font-size: .7rem;">{{ item.title }}</div>
                <div class="item-after">
                  <div v-if="item.value.add" style="float: left; font-size: .7rem;">{{ item.value.value }}</div>
                  <img src="/static/myIcons/right-arrow.svg" style="display: inline-block; float: right; padding-left: 5px; width: 21px; height: 21px"/>
                </div>
              </div>
            </div>
          </list-item>
        </list>
        <button-small caption="" @click.native="onProfileClicked"></button-small>
      </div>
    </page-content>
  </div>
</template>

<script>
  import PersonCard from 'appComponents/components/cards/PersonCard.vue'
  import ButtonSmall from 'appComponents/components/buttons/ButtonSmall.vue'
  import TitleHeader from 'appComponents/components/headers/TitleHeader.vue'
  import { List, ListItem } from '~/components/list'
  import Content from '~/components/content'

  export default {
    components: {
      ButtonSmall,
      PersonCard,
      TitleHeader,
      List,
      ListItem,
      'page-content': Content
    },
    data () {
      return {
        personData: {
          name: '',
          surname: '',
          patronymic: '',
          phone: '+7 692 680 28 75',
          bonuses: 2000,
          ordervalue: 6,
          favorites: 13,
          region: 'Saint-Petersburg'
        },
        menuData: [
          {
            title: '   ',
            value: {
              add: false,
              value: ''
            },
            link: ''
          },
          {
            title: '  E-mail ',
            value: {
              add: false,
              value: ''
            },
            link: ''
          },
          {
            title: '  ',
            value: {
              add: false,
              value: ''
            },
            link: ''
          },
          {
            title: '   ',
            value: {
              add: true,
              value: '5'
            },
            link: ''
          },
          {
            title: ' ',
            value: {
              add: true,
              value: '12'
            },
            link: ''
          },
          {
            title: ' ',
            value: {
              add: false,
              value: ''
            },
            link: ''
          },
          {
            title: ' ',
            value: {
              add: false,
              value: ''
            },
            link: ''
          },
          {
            title: '',
            value: {
              add: false,
              value: ''
            },
            link: ''
          },
          {
            title: 'FAQ',
            value: {
              add: false,
              value: ''
            },
            link: ''
          },
          {
            title: ' ',
            value: {
              add: true,
              value: '-'
            },
            link: ''
          }
        ]
      }
    },
    methods: {
      onProfileClicked () {
        this.$router.push({name: 'person'})
      },
      onItemClicked (item) {
        switch (item.title) {
          case '   ':
            this.$router.push({name: 'ordered'})
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .content
  {
    height: 40px;
    background-color: #0BB20C;
  }
</style>



